.headerBox {
  height: 100vh;

  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.headerTitleSec {
  position: relative;
  background-color: #e7e9eb;
  margin: 5px;
}
.headerTitleSec .floatMid {
  /* position: absolute; */
  margin-top: 2em;
  text-align: center;
}
.header-title {
  font-weight: bold;
}
.header-name {
  color: #011d3a;
}
.headerdescSec {
  margin-top: 2em;
}
.header-desc {
  text-align: justify;
  padding: 10px 15px;
  line-height: 1.3rem;
}

.sidePanelSec {
  height: 98vh;
  overflow: scroll;
}
.projectBox {
  margin-top: 2em;
}
.projectCards {
  padding: 10px;
  text-align: justify;
}
.projectCards a,
.projectCards a:hover {
  color: #000;
  text-decoration: none;
}

.geoBox {
  display: block;
  /* align-items: center;
  justify-content: center; */
  padding: 10px;
  margin: 20px 10px;
  border: 1px solid #dedede;
  float: left;
  clear: both;
}
.geoForm {
  display: block;
  border-bottom: 1px solid red;
  clear: both;
  padding: 10px;
}

.geoForm label {
  margin-right: 10px;
}
.geoBox .result {
  display: block;
  margin-top: 10px;
  padding: 10px;
  box-shadow: 0 0 2px #dedede;
}
.geoForm h6 {
  text-align: center;
}
.result div {
  display: block;
  width: 90%;
  height: 60px;
  border-bottom: 1px solid #dedede;
}
.loc-logo {
  float: left;
  width: 40px;
  height: 30px;
  color: gray;
}

/* Custom scroll bar */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #e7e9eb;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
